.card__loading-animation {
  width: 80px;
  height: 80px;
  z-index: 2;
  display: none;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-60%, -68%);
}

.card__loading-animation div {
  width: 16px;
  background: #fff;
  animation: card__loading-animation 1.2s cubic-bezier(0, .5, .5, 1) infinite;
  display: inline-block;
  position: absolute;
  left: 8px;
}

.card__loading-animation div:nth-child(1) {
  animation-delay: -.24s;
  left: 8px;
}

.card__loading-animation div:nth-child(2) {
  animation-delay: -.12s;
  left: 32px;
}

.card__loading-animation div:nth-child(3) {
  animation-delay: 0;
  left: 56px;
}

@keyframes card__loading-animation {
  0% {
    height: 64px;
    top: 8px;
  }

  50%, 100% {
    height: 32px;
    top: 24px;
  }
}

