  width: 45px;
  height: 62px;
}

.card__img {
  width: 100%;
  height: 100%;
}

.card {
  transform-style: preserve-3d;
  transition: transform .5s;
  position: absolute;
  box-shadow: 0 22px 70px 4px rgba(0, 0, 0, .56);
}

.card__front, .card__back {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 5px;
  position: absolute;
}

.card__front:hover, .card__front:focus {
  transform: scale(1.1);
}

.card__back {
  transform-origin: bottom;
  transform: rotateY(180deg);
}

.card__matched {
  z-index: 1;
  animation: moveSpinStack 1.2s forwards;
  box-shadow: 0 2px 4px rgba(255, 255, 255, .4), 0 7px 13px -3px rgba(255, 255, 255, .3), inset 0 -3px #060;
}

@keyframes moveSpinStack {
  0% {
    transform: rotate(0deg)scale(1);
  }

  25% {
    transform: rotate(360deg)scale(2);
  }

  50% {
    transform: rotate(720deg)scale(2);
  }

  75% {
    transform: rotate(1080deg)scale(2);
  }

  100% {
    top: 7%;
    right: 45%;
    transform: rotate(1480deg)scale(1);
  }
}

