  }

  .stats__content-container {
    background-color: getColor('marvel-tertiary-color');
    color: getColor('marvel-secondary-color');
    box-shadow: rgba(255, 0, 0, 0.25) 0px 54px 55px, rgba(255, 0, 0, 0.275) 0px -12px 30px, rgba(255, 0, 0, 0.292) 0px 4px 6px, rgba(255, 0, 0, 0.308) 0px 12px 13px, rgba(255, 0, 0, 0.309) 0px -3px 5px;
    border-radius: 1px;
  }

  .stats__modal-close {
    color: getColor('marvel-secondary-color');

    &:hover,
    &:focus {
      color: getColor('marvel-primary-color');
    }
  }

  .btn__clear-stats-memory {
    background-color: getColor('marvel-secondary-color');
    color: getColor('marvel-tertiary-color');

    &:hover,
    &:focus {
      background-color: getColor('marvel-primary-color');
    }
  }

  .header__section {
    background-color: getColor('marvel-tertiary-color');
  }

  .timer__wrapper,
  .score__wrapper {
    color: getColor('marvel-secondary-color');
    font-family: getFontFamily('classic-primary-font');
    box-shadow: rgb(255, 246, 117) 3px 3px 6px 0px inset, rgba(255, 246, 117, 0.5) -3px -3px 6px 1px inset;
  }

  .card-gallery__wrapper {
    background-image: linear-gradient(rgb(255, 230, 0), rgb(127, 129, 1));
    padding: 20px;
    box-shadow: rgba(0, 102, 0, 0.07) 0px 1px 2px, rgba(0, 102, 0, 0.07) 0px 2px 4px, rgba(0, 102, 0, 0.07) 0px 4px 8px, rgba(0, 102, 0, 0.07) 0px 8px 16px, rgba(0, 102, 0, 0.07) 0px 16px 32px, rgba(0, 102, 0, 0.07) 0px 32px 64px;
  }

  .pokemon-logo {
    display: none;
  }

  .card__container-marvel,
  .card__marvel,
  .card__img-marvel,
  .card__front-marvel,
  .card__back-marvel {
    width: 48px;
    height: 58px;
    border-radius: 10px;
  }

  .card__front-marvel {
    background-image: url("/src/images/marvel-comics-logo.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: getColor('marvel-tertiary-color');
  }

  .footer__section {
    background-color: getColor('marvel-tertiary-color');
  }

  .game-controls__button,
  .hint__button {
    color: getColor('marvel-secondary-color');
    box-shadow: rgba(250, 246, 0, 0.4) 0px 2px 4px, rgba(250, 246, 0, 0.583) 0px 7px 13px -3px, rgba(250, 246, 0, 0.2) 0px -3px 0px inset;

    &:hover,
    &:focus {
      box-shadow: rgba(255, 244, 117, 0.379) 0px 1px 0px, rgba(255, 246, 143, 0.1) 0px 8px 24px, rgba(255, 246, 143, 0.1) 0px 16px 48px;
    }

    .tooltiptext {
      color: getColor('marvel-secondary-color');
      background-color: getColor('marvel-primary-color');
      border: 1px solid getColor('marvel-tertiary-color');

      &:after {
        border-bottom: 20px solid getColor('marvel-primary-color');
      }
    }
  }

  .game-menu__btn {
    box-shadow: rgba(250, 246, 0, 0.4) 0px 2px 4px, rgba(250, 246, 0, 0.583) 0px 7px 13px -3px, rgba(250, 246, 0, 0.2) 0px -3px 0px inset;

    &:hover,
    &:focus {
      box-shadow: rgba(255, 244, 117, 0.379) 0px 1px 0px, rgba(255, 246, 143, 0.1) 0px 8px 24px, rgba(255, 246, 143, 0.1) 0px 16px 48px;
    }

    .tooltiptext {
      color: getColor('marvel-secondary-color');
      background-color: getColor('marvel-primary-color');
      border: 1px solid getColor('marvel-tertiary-color');

      &:after {
        border-top: 20px solid getColor('marvel-primary-color');
      }
    }
  }

  .game-menu-toggle__icon {
    fill: getColor('marvel-secondary-color');
  }

  .page-title__text {
    color: getColor('marvel-secondary-color');
  }

  .footer__contacts-item {
    color: getColor('marvel-secondary-color');
  }

  .social-icon {
    color: getColor('marvel-secondary-color');
  }

  .menu-toggle__open {
    box-shadow: rgba(250, 246, 0, 0.4) 0px 2px 4px, rgba(250, 246, 0, 0.583) 0px 7px 13px -3px, rgba(250, 246, 0, 0.2) 0px -3px 0px inset;

    &:hover,
    &:focus {
      box-shadow: rgba(255, 244, 117, 0.379) 0px 1px 0px, rgba(255, 246, 143, 0.1) 0px 8px 24px, rgba(255, 246, 143, 0.1) 0px 16px 48px;
    }
  }

  .menu-toggle__icon-open {
    fill: getColor('marvel-secondary-color');
  }

  .menu-container {
    background-color: getColor('marvel-primary-color');
  }

  .mobile-menu-content__container {
    background-color: getColor('marvel-secondary-color');
    box-shadow: rgb(205, 210, 104) 0px 20px 30px -10px;
  }

  .menu-toggle {
    background-color: getColor('marvel-secondary-color');
  }

  @media screen and (min-width: 768px) {

    .card__container-marvel,
    .card__marvel,
    .card__img-marvel,
    .card__front-marvel,
    .card__back-marvel {
      width: 84px;
      height: 118px;
    }
  }

  @media screen and (min-width: 1200px) {

    .card-gallery {
      gap: 40px;
    }

    .card__container-marvel,
    .card__marvel,
    .card__img-marvel,
    .card__front-marvel,
    .card__back-marvel {
      width: 140px;
      height: 160px;
    }
  }
}